@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);

* {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
}

button:focus, input:focus {
  outline: 0;
}

.navbar {
  width: 100%;
  height: 4rem;
  position: fixed;
  background-color: #00275e;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  letter-spacing: 1px;
  z-index: 100;
}

.logo {
  height: 3.5rem;
  width: auto;
  margin-left: 2rem;
  margin-top: 0.25rem;
}

.nav-links {
  list-style: none;
  display: flex;
  margin-left: auto;
  margin-right: 2rem;
}

.nav-links a {
  color: #fff;
  text-decoration: none;
  margin: 1rem;
  transition: 0.25s;
}

.nav-links a:hover {
  color: #eb773a;
  transition: 0.25s;
}

@media screen and (max-width: 800px) {
  .logo {
    display: none;
  }

  .nav-links {
    margin: auto;
  }
}
.home-container {
    height: 100vh;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr;
    justify-content: center;
    align-items: center;
    color: #00275E;
    font-weight: 700;
}

.home-text {
    display: flex;
    flex-direction: column;
    padding: 0 4rem 0 6rem;
}

.home-text p {
    font-size: 32px;
}

.home-btn {
    width: 5rem;
    margin-top: 1.5rem;
    padding: 1rem;
    background-color: #FFAE46;
    text-align: center;
    text-decoration: none;
    font-size: 20px;
    color: #00275E;
    transition: 0.4s;
}

.home-btn:hover {
    background-color: #EB773A;
    transition: 0.4s;
}

.rocket {
    margin-top: 2rem;
    width: 80%;
}

@media screen and (max-width: 800px) {
    .home-container {
        height: 100vh;
        display: flex;
        flex-direction: column;
        text-align: center;
    }

    .home-text {
        padding: 2rem 3rem 2rem 3rem;
    }

    .home-text p {
      font-size: 18px;
    }
    .home-btn {
        align-self: center;
    }

    .rocket {
        margin: auto;
        margin-top: 1rem;
        margin-bottom: 0;
        width: 80%;
        order: -1;
    }
  }
.about-container {
    height: 100vh;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr;
    justify-content: center;
    align-items: center;
    color: #00275E;
    font-weight: 700;
}

.about-text {
    display: flex;
    flex-direction: column;
    margin: -5rem 4rem 0 6rem;
}

.about-text p {
    font-size: 32px;
}

.astronaut {
    margin-top: 2rem;
    width: 80%;
}

@media screen and (max-width: 800px) {
    .about-container {
        height: 100vh;
        display: flex;
        flex-direction: column;
        text-align: center;
    }

    .about-text {
        margin: 0;
        padding: 2rem 3rem 2rem 3rem;
    }

    .about-text p {
      font-size: 18px;
    }

    .astronaut {
        margin: auto;
        margin-top: 1rem;
        margin-bottom: 0;
        width: 80%;
        order: -1;
    }
}
.pictures-container {
  text-align: center;
  align-items: center;
}

.searchbar {
  width: 50%;
  height: 3rem;
  border: 1px solid #a4a4a4;
  color: #00275e;
  border-radius: 50px;
  font-size: 20px;
  padding: 8px 20px;
  margin-top: 6rem;
  margin-bottom: 2rem;
  outline: none;
}

.cards-items {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.cards-item {
  margin: 0 1rem;
  background-color: #fff;
  border-radius: 40px;
  width: 350px;
  height: 350px;
  box-shadow: 8px 8px 16px rgba(0, 0, 0, 0.25);
  justify-content: center;
  margin: 2rem;
}

.cards-item-img {
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  object-fit: cover;
  border-radius: 40px;
}


.notfound {
    padding-top: 6rem;
    text-align: center;
    font-size: 20px;
    color: #00275E;
}
